#settings-container {
  margin: 10px;
  padding: 30px;
  width: 500px;
  height: 200px;
  background-color: white;
  border: 1px solid gray;
  border-radius: 5px;
  -webkit-box-shadow: 1px 2px 7px gray;
          box-shadow: 1px 2px 7px gray;

  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

h2#settings-title {
  margin-top: 0;
}

input {
  width: 50%;
  height: 35px;
  margin: 5px;
}

div.update-form-container {
  width: 100%;
  text-align: center;
}

div.update-form-button {
  width: 155px;
}

div#delete-account-button {
  margin: 10px 0 5px;
  background-color: #c82333;
}
div#delete-account-button:active {
  -webkit-box-shadow: 0 0;
          box-shadow: 0 0;
  background: #7c1721;
}



